<template>
  <div id="product-modify">
    <form @submit="onCancel">
      <div class="d-flex justify-content-between align-items-center">
        <h3>{{ titlePage }} Product</h3>
        <div class="d-flex">
          <router-link v-bind:to="{ path: cancelURL }" class="btn"
            >Cancel</router-link
          >
          <button class="btn btn-success" type="submit" @click="onSave">
            {{ titleBtn }}
          </button>
        </div>
      </div>
      <div class="card mt-3">
        <div class="card-header"><strong>General Information</strong></div>
        <div class="card-body">
          <div class="row">
            <div class="col-12 col-md-6">
              <div role="group" class="form-group">
                <label for="uid-qvk4gvm5sa" class="">ID *</label>
                <input
                  type="text"
                  placeholder="Enter product name"
                  class="form-control"
                  v-model="id"
                  required
                  disabled
                />
              </div>
            </div>
            <div class="col-12 col-md-6">
              <div role="group" class="form-group">
                <label for="uid-qvk4gvm5sa" class="">Name *</label>
                <input
                  type="text"
                  placeholder="Enter product name"
                  class="form-control"
                  required
                  v-model="name"
                />
              </div>
            </div>
            <div class="col-12 col-md-6">
              <div role="group" class="form-group">
                <label for="uid-qvk4gvm5sa" class="">Type *</label>
                <input
                  type="text"
                  placeholder="Enter product type"
                  class="form-control"
                  required
                  v-model="type"
                />
              </div>
            </div>
            <div class="col-12 col-md-6">
              <div role="group" class="form-group">
                <label for="uid-qvk4gvm5sa" class="">Orders *</label>
                <input
                  type="number"
                  placeholder="Enter product type"
                  class="form-control"
                  v-model="orders"
                />
              </div>
            </div>
            <div class="col-12 col-md-6">
              <div role="group" class="form-group">
                <label for="uid-qvk4gvm5sa" class=""
                  >Warranty Period(month):</label
                >
                <input
                  type="number"
                  placeholder="Enter warranty period"
                  class="form-control"
                  v-model="warrantyPeriod"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card mt-3">
        <div class="card-header"><strong>Specifications</strong></div>
        <div class="card-body">
          <div class="row">
            <div class="col-12 col-md-6">
              <div role="group" class="form-group">
                <label for="uid-qvk4gvm5sa" class=""
                  >Smallest Specifications</label
                >
                <input
                  type="text"
                  placeholder="Enter Smallest Specifications"
                  class="form-control"
                  v-model="specifications.smallestSpec"
                />
              </div>
            </div>
            <div class="col-12 col-md-6">
              <div role="group" class="form-group">
                <label for="uid-qvk4gvm5sa" class="">Smallest Unit</label>
                <input
                  type="text"
                  placeholder="Enter Smallest Specifications Unit"
                  class="form-control"
                  v-model="specifications.unit"
                />
              </div>
            </div>
            <div class="col-12 col-md-6">
              <div role="group" class="form-group">
                <label for="uid-qvk4gvm5sa" class="">Smallest Specifications Weight</label>
                <input
                  type="text"
                  placeholder="Enter Smallest Specifications Weight"
                  class="form-control"
                  v-model="specifications.weight"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card mt-3">
        <div class="card-header"><strong>Price Information</strong></div>
        <div class="card-body">
          <div class="row">
            <div class="col-12 col-md-6">
              <div role="group" class="form-group">
                <label for="uid-qvk4gvm5sa" class="">Wholesale</label>
                <input
                  type="text"
                  placeholder="Enter product name"
                  class="form-control"
                  v-model="price.wholesale"
                />
              </div>
            </div>
            <div class="col-12 col-md-6">
              <div role="group" class="form-group">
                <label for="uid-qvk4gvm5sa" class="">Retail</label>
                <input
                  type="text"
                  placeholder="Enter product name"
                  class="form-control"
                  v-model="price.retail"
                />
              </div>
            </div>
            <div class="col-12 col-md-6">
              <div role="group" class="form-group">
                <label for="uid-qvk4gvm5sa" class="">Distribution</label>
                <input
                  type="text"
                  placeholder="Enter product type"
                  class="form-control"
                  v-model="price.distribution"
                />
              </div>
            </div>
            <div class="col-12 col-md-6">
              <div role="group" class="form-group">
                <label for="uid-qvk4gvm5sa" class="">Agent</label>
                <input
                  type="text"
                  placeholder="Enter product type"
                  class="form-control"
                  v-model="price.agent"
                />
              </div>
            </div>
            <div class="col-12 col-md-6">
              <div role="group" class="form-group">
                <label for="uid-qvk4gvm5sa" class=""
                  >Smallest specifications</label
                >
                <input
                  type="text"
                  placeholder="Enter product type"
                  class="form-control"
                  v-model="price.smallestSpec"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </form>
    <Modals
      :warningModal="modalShow"
      :message="modalMessage"
      :title="'Cancel'"
    />
  </div>
</template>

<script>
import Util from "../../util";
import Modals from "../modal/ModalConfirm";
import { db } from "../../configs/firebaseInit";

export default {
  name: "product-modify",
  components: {
    Modals,
  },
  data() {
    return {
      modalShow: false,
      modalMessage: "Your changes could not be saved if you choose to leave.",
      titlePage: null,
      titleBtn: null,
      cancelURL: null,
      modifyMode: null,
      validated: false,
      submitted: false,
      lastID: null,
      id: null,
      name: null,
      type: null,
      orders: 0,
      warrantyPeriod: null,
      price: {
        wholesale: 0,
        retail: 0,
        distribution: 0,
        agent: 0,
        smallestSpec: 0,
      },
      specifications: {
        smallestSpec: null,
        unit: null,
        weight: null,
      },
    };
  },
  beforeRouteEnter(to, from, next) {
    let count = 0;
    if (!to.params.id) {
      db.collection("products")
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach(() => {
            count++;
            next((vm) => {
              vm.modifyMode = "create";
              vm.titlePage = "Create";
              vm.titleBtn = "Create";
              vm.id = Util.generateID("product", count + 1);
              vm.cancelURL = "/products";
            });
          });
        });
    } else {
      db.collection("products")
        .where("id", "==", to.params.id)
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            next((vm) => {
              vm.modifyMode = "edit";
              vm.titlePage = "Edit";
              vm.titleBtn = "Save";
              vm.id = doc.data().id;
              vm.cancelURL = "/products/" + vm.id;
              vm.name = doc.data().name;
              vm.type = doc.data().type;
              vm.orders = doc.data().orders ? doc.data().orders : 0;
              vm.price.wholesale = doc.data().price
                ? doc.data().price.wholesale
                : 0;
              vm.price.retail = doc.data().price ? doc.data().price.retail : 0;
              vm.price.distribution = doc.data().price
                ? doc.data().price.distribution
                : 0;
              vm.price.agent = doc.data().price ? doc.data().price.agent : 0;
              vm.price.smallestSpec = doc.data().price
                ? doc.data().price.smallestSpec
                : 0;
            });
          });
        });
    }
  },
  watch: { $route: "fetchDatam, getLastID" },
  methods: {
    fetchData() {
      db.collection("products")
        .where("id", "==", this.$route.params.id)
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.id = doc.data().id;
            this.name = doc.data().name;
            this.type = doc.data().type;
            this.orders = doc.data().orders;
            this.orders = doc.data().orders ? doc.data().orders : 0;
            this.price.wholesale = doc.data().price
              ? doc.data().price.wholesale
              : 0;
            this.price.retail = doc.data().price ? doc.data().price.retail : 0;
            this.price.distribution = doc.data().price
              ? doc.data().price.distribution
              : 0;
            this.price.agent = doc.data().price ? doc.data().price.agent : 0;
            this.price.smallestSpec = doc.data().price
              ? doc.data().price.smallestSpec
              : 0;
          });
        });
    },
    onCancel() {
      this.$router.push(this.cancelURL);
      // this.modalShow = true;
    },
    onSave() {
      this.submitted = true;
      if (this.id && this.name && this.type) {
        this.validated = true;
        if (this.submitted && this.validated) {
          switch (this.modifyMode) {
            case "create":
              {
                db.collection("products")
                  .add({
                    id: this.id,
                    name: this.name,
                    type: this.type,
                    orders: this.orders ? this.orders : 0,
                    price: {
                      wholesale: this.price.wholesale
                        ? this.price.wholesale
                        : 0,
                      retail: this.price.retail ? this.price.retail : 0,
                      distribution: this.price.distribution
                        ? this.price.distribution
                        : 0,
                      agent: this.price ? this.price.agent : 0,
                      smallestSpec: this.price ? this.price.smallestSpec : 0,
                    },
                  })
                  .then(() => {
                    this.$router.push("/products/" + this.id);
                  });
              }
              break;
            case "edit":
              {
                db.collection("products")
                  .where("id", "==", this.id)
                  .get()
                  .then((querySnapshot) => {
                    querySnapshot.forEach((doc) => {
                      doc.ref.update({
                        name: this.name,
                        type: this.type,
                        orders: this.orders ? this.orders : 0,
                        price: {
                          wholesale: this.price.wholesale
                            ? this.price.wholesale
                            : 0,
                          retail: this.price.retail ? this.price.retail : 0,
                          distribution: this.price.distribution
                            ? this.price.distribution
                            : 0,
                          agent: this.price ? this.price.agent : 0,
                          smallestSpec: this.price
                            ? this.price.smallestSpec
                            : 0,
                        },
                      });
                      this.$router.push("/products/" + this.id);
                    });
                  });
              }
              break;
          }
        }
      } else {
        this.submitted = true;
        this.validated = false;
        return;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
label {
  font-weight: 700;
}
</style>